.FormulaContainer {
    display: flex;
    justify-content: center;
    flex-flow: column;
    padding: 3%;

    .InputIngredientes {
        margin-bottom: 5px;
        display: flex;
    }
    
    .FormulaBalance {
        display: flex;
        justify-content: space-between;
        .FormulaTable {
            width: 60%;
        }
        .Balance {
            width: 30%;
            .MuiTypography-body1 {
                width: 100%
            }
        }
        .BalanceCard {
            margin: 1%;
            p {
                color: #A3ABD7;
            }
            p:first-child {
                color: #7585d8;
            }    
            }
        .IngredientesContainer {
            display: flex;
            flex-flow: column;

 
        }
    }
}

.AddIngrediente {
    display: flex;
}